import React from 'react';
import { DatePicker, Input } from 'antd';
import { FormItem } from '../FormItem/FormItem';
import * as S from './style';
import { Dayjs } from 'dayjs';

import { useDispatch } from 'react-redux';
import { updateWorkplace, WorkPlace } from '../../store/reducers/WorkExpSlice';

const { RangePicker } = DatePicker;
const { TextArea } = Input;

interface WorkProps {
    index: number;
    workplace: WorkPlace;
}

export const WorkExpItem: React.FC<WorkProps> = ({ index }) => {
    const dispatch = useDispatch();

    const handleCompanyChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        dispatch(
            updateWorkplace({ index, workplace: { company: e.target.value } })
        );
    };

    const handleJobChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        dispatch(
            updateWorkplace({ index, workplace: { job: e.target.value } })
        );
    };

    const handleWorkPeriodChange = (
        dates: [Dayjs | null, Dayjs | null],
        dateStrings: [string, string]
    ) => {
        const [start, end] = dateStrings;
        dispatch(
            updateWorkplace({
                index,
                workplace: { workPeriodStart: start, workPeriodEnd: end }
            })
        );
    };

    const handleDescriptionChange = (
        e: React.ChangeEvent<HTMLTextAreaElement>
    ) => {
        dispatch(
            updateWorkplace({
                index,
                workplace: { description: e.target.value }
            })
        );
    };

    return (
        <>
            <S.Header>  - {index + 1}</S.Header>
            <FormItem label="">
                <Input placeholder="" onChange={handleCompanyChange} />
            </FormItem>
            <FormItem label="">
                <Input placeholder="" onChange={handleJobChange} />
            </FormItem>
            <FormItem label=" ">
                <RangePicker
                    placeholder={['', '']}
                    onChange={handleWorkPeriodChange}
                />
            </FormItem>
            <FormItem label="">
                <TextArea rows={5} onChange={handleDescriptionChange} />
            </FormItem>
        </>
    );
};
